 .App{
  font-family: Arial, sans-serif;
  margin: 0;
  padding: 0;
  display: flex;
  flex-direction: column;
  align-items: center;
  background-color: #f5f5f5;
}

#navbar {
  width: 100%;
  background-color: #333;
  overflow: hidden;
}

#navbar button {
  float: left;
  display: block;
  color: white;
  text-align: center;
  padding: 14px 16px;
  text-decoration: none;
  font-size: 17px;
  border: none;
  background: none;
  cursor: pointer;
}

#navbar button:hover {
  background-color: #ddd;
  color: black;
}

#navbar button.active {
  background-color: #8c00ff;
  color: #f5fdfd;
}


.page {
  padding: 20px;
  margin-top: 20px;
  background: white;
  width: 50%;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
}

label {
  display: block;
  margin-top: 10px;
}

input[type="text"], input[type="file"] {
  width: 100%;
  padding: 10px;
  margin: 10px 0;
  box-sizing: border-box;
}

button {
  background-color: #4CAF50;
  color: white;
  padding: 10px 20px;
  border: none;
  cursor: pointer;
  margin-top: 10px;
}

button:hover {
  background-color: #45a049;
}
.note {
  margin-top: 20px;
  padding: 10px;
  background-color: #ffefc1;
  border: 1px solid #ffd700;
  width: 50%;
  text-align: center;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
}

.heading {
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 20px 0;
}

.heading h1 {
  display: flex;
  align-items: center;
  font-size: 24px;
  margin: 0;
}

.logo {
  height: 50px;
  margin-left: 10px;
}

footer {
  margin-top: 20px;
  padding: 10px;
  background-color: #f8f9fa;
  text-align: center;
  font-size: 14px;
}

#responsePage {
  text-align: center;
  background: white;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
  margin: 10%  30%;
  padding-bottom: 20px;
}

#response-data {
  font-size: 18px;
  font-weight: bold;
  margin-bottom: 20px;
  padding-bottom: 20px;
}

select#processSelect {
  width: 100%;
  padding: 10px;
  border: 1px solid #ccc;
  border-radius: 5px;
  font-size: 16px;
  margin-top: 10px;
}

select#processSelect option {
  padding: 10px;
}

.top-right-button {
  position: absolute;
  right: 10px; /* Distance from the right */
  padding: 10px 20px;
    border: none;
    border-radius: 12px;
    background: linear-gradient(45deg, #6a11cb, #2575fc);
    color: white;
    font-size: 16px;
    cursor: pointer;
    overflow: hidden;
    outline: none;
    transition: background 0.3s, transform 0.3s;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2);
}

.navbar-active {
  background: #1976d2 !important;
  color: white !important;
}